import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getSalaryFrequencyConfig = paramsSearch =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const createSalaryFrequencyConfig = (newItem) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig`,
    data: { ...newItem },
    method: 'POST',
  });

export const deleteSalaryFrequencyConfig = (id) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    method: 'DELETE',
  });

export const getSalaryFrequencyConfigId = id =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    method: 'GET',
  });

export const updateSalaryFrequencyConfig = (id, editDoc) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    data: { ...editDoc },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportSalaryFrequencyConfigs.xlsx',
    url: `/SalaryFrequencyConfig/ExportSalaryFrequencyConfigs`,
    data: { ...paramsSearch },
    method: 'POST',
  });



