import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
// import BtnAddNew from 'components/common/button/BtnAddNew';
// import YearSemesterInput from 'components/common/input/YearSemesterInput';
// import { getCodeStudent, getBonusType, getBonusTypeChild } from 'services/AddReward';
// import DatePickerString from 'components/common/input/DatePickerString';

import {
  createSalaryFrequencyConfig,
  getSalaryFrequencyConfigId,
  updateSalaryFrequencyConfig,
} from 'services/salaryFrequencyConfigService';

// const { Option } = Select;
const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };
  componentDidMount = () => {};

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getDataById(match.params.id);
    }
  };
  getDataById = async id => {
    const res = await getSalaryFrequencyConfigId(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    const { match } = this.props;
    const { teacherId } = this.state;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateSalaryFrequencyConfig(teacherId, values)
            .then(() => {
              message.success('Chỉnh sửa hồ sơ đi thành công');
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('error', error);
              // message.error(error.response.data.message);
            });
        } else {
          createSalaryFrequencyConfig(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { initialValue, teacherId } = this.state;
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            {teacherId ? (
              <Form.Item style={{ display: 'none' }} hasFeedback label="Vị trí ">
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            ) : null}
            <Form.Item hasFeedback label="Vị trí ">
              {getFieldDecorator('positionName', {
                initialValue: initialValue.positionName,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Số năm định kỳ tăng lương ">
              {getFieldDecorator('frequencyYear', {
                initialValue: initialValue.frequencyYear,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Hệ số tăng định kỳ">
              {getFieldDecorator('frequencyPoint', {
                initialValue: initialValue.frequencyPoint,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Hệ số khởi điểm">
              {getFieldDecorator('firstLevelPoint', {
                initialValue: initialValue.firstLevelPoint,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Số bậc tối đa">
              {getFieldDecorator('maxLevel', {
                initialValue: initialValue.maxLevel,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



